html {
	position: relative;
	min-width: 100%;
	min-height: 100%;
}

body {
	min-width: 100%;
	min-height:100%;
	padding: 80px 0px;
	background-color: #fff;
	background: radial-gradient(top, circle cover,#c3deee 30%,#7da2e8);
	background: -o-radial-gradient(top, circle cover,#c3deee 30%,#7da2e8);
	background: -webkit-radial-gradient(top, circle cover,#c3deee 30%,#7da2e8);
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 30px;
}

@-webkit-keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



