.card-view {
	height: 150px;
	-webkit-box-shadow: 0 0 2px 0 rgba(119, 119, 119, 0.3);
	        box-shadow: 0 0 2px 0 rgba(119, 119, 119, 0.3);
    color: black;
	border-radius: 10px;
	-webkit-transition: -webkit-box-shadow 1s;
	transition: -webkit-box-shadow 1s;
	-o-transition: box-shadow 1s;
	transition: box-shadow 1s;
	transition: box-shadow 1s, -webkit-box-shadow 1s;
	margin: 10px 0;
	text-align: center;
}

a:hover {
	text-decoration: none;
}

a:hover .card-view {
	-webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3), 0 10px 20px rgba(0, 0, 0, 0.6);
	        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3), 0 10px 20px rgba(0, 0, 0, 0.6);
}


