.footer {
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 40px;
	line-height: 20px;
}

.footer-container {
    text-align: center;
    margin: 0 auto;
    position: relative;
    color: white;
}

.heart{
	color: #EB5E28;
    -webkit-animation: heathing 1s ease infinite;
            animation: heathing 1s ease infinite;
}

@-webkit-keyframes heathing{
    0% {
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
    20% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    40% {
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
    60% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    80% {
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
    100% {
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
}

@keyframes heathing{
    0% {
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
    20% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    40% {
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
    60% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    80% {
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
    100% {
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
}



